import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { AddBlockWrapperComponent } from './components/add-block-wrapper/add-block-wrapper.component';
import { AddUnitWrapperComponent } from './components/add-unit-wrapper/add-unit-wrapper.component';
import { AddFloorUnitComponent } from './components/add-floor-unit/add-floor-unit.component';

const routes: Routes = [
	{ path: '', redirectTo: 'add-block', pathMatch: 'full'},
	{ path: 'add-block', component: AddBlockWrapperComponent},
	{ path: 'add-unit', component: AddUnitWrapperComponent},
	{ path: 'add-floor-unit', component: AddFloorUnitComponent},
	{ path: '**', redirectTo: 'add-block', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProfileSettingsRoutingModule { }
