import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import {FormsModule} from '@angular/forms';

import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';

import { ApartmentBlockUnit_model } from 'src/app/api/defs/ApartmentBlockUnit_model';

@Component({
  selector: 'app-add-unit',
  templateUrl: './add-unit.component.html',
  styleUrls: ['./add-unit.component.scss']
})
export class AddUnitComponent implements OnInit {

  unit:any;
  unitBlocksData:any;
  unitTypeData:any;

  unitType:string = "";

  isUnitSubmitted:boolean = false;
  isUnitAdded:boolean = false;

  apartmentBlockUnit:any

  @Input ('index') index;
  @Input ('array') unitArray;
  @Input() blockId;

  @Output() public outputParams = new EventEmitter();

  constructor(
    private apartmentService:ApartmentService,
    private lookupService:LookupService,
    private cookieService:CookieService
  ) { }

  isArrayOne(){
  	return this.unitArray.length > 1 ? true : false;
  }

  deleteUnit(){
  	this.unitArray.splice(this.index, 1);
  	this.outputParams.emit(this.unitArray);
  }

  isMobileView() {
    return window.innerWidth <= 576;
  }

  submitAddUnitForm(form) {

  	this.isUnitSubmitted = true;

    this.apartmentBlockUnit = {
      "apartmentBlockUnitId": 1,
      "apartmentBlockUnitNumber": this.unit.no,
      "apartmentBlockId": parseInt(this.blockId),
      "unitTypeId": parseInt(this.unitType),
      "builtupArea": parseInt(this.unit.builtArea),
      "carpetArea": parseInt(this.unit.carpetArea),
      "rentAmount": parseInt(this.unit.rentAmount),
      "advanceAmount": parseInt(this.unit.advanceAmount),
      "propertyManagerId": null,
      "intercom": this.unit.intercom,
	  "cars": this.unit.car,
      "isActive": true,
      "isAvailableForRent": true,
	  "isPetsAllowed": true,
	  "isFurnished": true,
	  "isVacant": true,
	  insertedBy: 1
    }

    let params = {
      apartmentBlockUnits: this.apartmentBlockUnit
    }
    
    this.apartmentService.addApartmentBlockUnit(params).subscribe((res:any) => {
      
      this.isUnitAdded = true;

    });


  }

  ngOnInit() {

    this.unit = {};
    
    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    this.apartmentService.getApartmentBlockByApartmentId(params).subscribe((res:any) => {
      this.unitBlocksData = res;
    });

    let unitParams = {
      LookupTypeId: 1
    }

    this.lookupService.getLookupValueByLookupTypeId(unitParams).subscribe((res:any) => {
    	this.unitTypeData = res;
    });

  }

}
