import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import {FormsModule} from '@angular/forms';

import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { SharedService } from '../../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import { ApartmentBlock_model } from 'src/app/api/defs/ApartmentBlock_model';


@Component({
  selector: 'app-add-block',
  templateUrl: './add-block.component.html',
  styleUrls: ['./add-block.component.scss']
})
export class AddBlockComponent implements OnInit {

  blockNo:string="";
  blockDescription:string = "";

  apartmentBlock:any;

  isBlockSubmitted:boolean = false;
  isBlockAdded:boolean = false;

  @Input ('index') index;
  @Input ('array') blockArray;

  @Output() public outputParams = new EventEmitter();

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
    private apartmentService:ApartmentService,
  	private sharedService:SharedService,
    private cookieService:CookieService
  ) { }

  isArrayOne(){
  	return this.blockArray.length > 1 ? true : false;
  }

  deleteBlock(){
  	this.blockArray.splice(this.index, 1);
  	this.outputParams.emit(this.blockArray);
  }

  isMobileView() {
    return window.innerWidth <= 576;
  }

  submitAddBlockForm(form) {

  	this.isBlockSubmitted = true;

    this.apartmentBlock = {
      apartmentBlockNumber: this.blockNo,
      description: this.blockDescription,
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      isActive: true,
      insertedBy: 1
    }

    let params = {
      apartmentBlocks: this.apartmentBlock
    }

    this.apartmentService.addApartmentBlock(params).subscribe((res:any) => {
    	this.isBlockAdded = true;
	},
	error => {
		console.log(error);
	});

  }

  ngOnInit() {

  	

  }

}
