import { Component, OnInit } from '@angular/core';

import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { CookieService } from 'ngx-cookie-service';


@Component({
  selector: 'app-add-unit-wrapper',
  templateUrl: './add-unit-wrapper.component.html',
  styleUrls: ['./add-unit-wrapper.component.scss']
})
export class AddUnitWrapperComponent implements OnInit {

  unitBlocksData:any;
  blockId:string = "";

  isBlockSelected:boolean = false;

  unitArray:any;
  unitCount:number = 1;
  isEditUser:boolean = false;

  constructor(
    private apartmentService:ApartmentService,
    private cookieService:CookieService
  ) { }

  chooseBlock() {
    this.isBlockSelected = true;
    this.unitCount=1;
    this.unitArray =  Array(this.unitCount).fill(0).map((x,i)=>i);
  }

  addUnit() {
    this.unitCount++;
    this.unitArray =  Array(this.unitCount).fill(0).map((x,i)=>i);
  }

  getUnitArray(event){
    this.unitArray = event;
    this.unitCount = this.unitArray.length;
  }

  isMobileView() {
    return window.innerWidth <= 576;
  }

  ngOnInit() {

    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    this.apartmentService.getApartmentBlockByApartmentId(params).subscribe((res:any) => {
      this.unitBlocksData = res;
    });

    this.unitArray =  Array(this.unitCount).fill(0).map((x,i)=>i)

  }

}
